footer {
  .grid-column {
    @include grid-media($sm-grid) {
      @include grid-column(12 of 12);
    }
    @include grid-media($lg-grid) {
      @include grid-column(14 of 16);
      @include grid-push(1, $lg-grid);
    }
    @include grid-media($xl-grid) {
      @include grid-column(14 of 16);
      @include grid-push(1, $xl-grid);
    }
  }

  @include breakpoint(300px, 700px) {
    .va-wrapper {
      padding-top: 27px;
      padding-bottom: 16px;
      text-align: left;
    }
    .left p, .right p {
      // @include vertical-align();
      margin: 0;
      text-decoration: none;
      color: $text;
      font-weight: 100;
      font-size: 12px;
      letter-spacing: 1px;
      display: inline-block;
    }
  }

  @include breakpoint(701px) {
    .va-wrapper {
      height: 72px;
      text-align: center;
    }
    .left {
      float: left;
      height: 100%;
    }
    .right {
      float: right;
      height: 100%;
    }
    .left p, .right p {
      @include vertical-align();
      margin: 0;
      text-decoration: none;
      color: $text;
      font-size: 12px;
      font-weight: 100;
      letter-spacing: 1px;
      display: inline-block;
    }
  }


}





